@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Lobster+Two&family=Quicksand:wght@300&family=Ubuntu&display=swap');
/* all cards container */

.card_container_init {
 width: 100%;
 height: 100vh;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 position: relative;
 background: url('../assets/card1.jpg') center center fixed;
 -webkit-background-size: cover;
 -moz-background-size: cover;
 -o-background-size: cover;
 background-size: cover;
}

.card_container_next {
 width: 100%;
 height: 100vh;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 position: relative;
 background: url('../assets/card.png') center center fixed;
 -webkit-background-size: cover;
 -moz-background-size: cover;
 -o-background-size: cover;
 background-size: cover;
 transition: .5s;
}

/* Card0 */

.counter {
 width: 20px;
 height: 20px;
 position: absolute;
 top: 70px;
 left: 330px;
 border: 2px solid black;
 border-radius: 50%;
 text-align: center;
 font-family: 'Great Vibes', cursive;
 font-weight: bold;
 font-size: 19px;
 

}

.text_names_container {
 width: 70%;
 height: 350px;
 display: flex;
 flex-direction: column;
 align-items: center;
 text-align: center;
 font-family: 'Great Vibes', cursive;
 font-weight: bold;
 font-size: 28px;
 margin: 0 auto;
}

.text_name_section {
 margin: 12px 0px;
}

.text_name_section > p {
 margin: 3px ;
}

.last_line {
 font-size: 20px;
}

/* Card1 */
.card_1_container {
 width: 100%;
 height: 100%;
 position: relative;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}

.back_card1 > img {
 width: 25px;
 position: absolute;
 top: 55px;
 left: 50px;
 z-index: 200;
}

.container_input_msg {
 width: 60%;
 height: 100px;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}

.container_code_input {
 width: 100%;
 height: 100px;
 display: flex;
 justify-content: space-between;
 align-items: center;
}



.container_input {
 width: 76%;
}

.input_code {
 width: 100%;
 height: 40px;
 color: #FFFF;
 font-family: 'Ubuntu', sans-serif;
 font-size: 18px;
 font-weight: 600;
 background-image: linear-gradient(#B38D5D, #FDC891);
 outline: none;
 border: 0;
 border-radius: 5px 0px 0px 5px;
 padding-left: 10px;
}

.btn_send {
 width: 18%;
 height: 42px;
 display: flex;
 align-items: center;
 justify-content: center;
 background-image: linear-gradient(#B38D5D, #FDC891);
 border-radius:  0px 5px 5px 0px;
}

.btn_send > img {
 width: 25px;
 
}

/*  CARD2 */

.second_card_content {
 width: 80%;
 height: 390px;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: space-between;
}

.text_msg_container {
 width: 70%;
 font-family: 'Ubuntu', sans-serif;
 text-align: start;
 margin-top: 20px;
 margin-left: 20px;
}

.video_container {
 width: 80%;
 display: flex;
 position: relative;
 justify-content: center;
 align-items: center;
 padding: 10px;
 margin-bottom: 20px;
 
}

.video_container > img {
 width: 100%;
 position: absolute;
 border-radius: 7px;
 box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.25);
}

.video_back {
 width: 90%;
 height: 150px;
 position: absolute;
 display: flex;
 justify-content: center;
 align-items: center;
 

}

.video_back > img {
 width: 50px;
}

.go_to_invitation {
 width: 100%;
 display: flex;
 justify-content: center;
 font-family: 'Ubuntu', sans-serif;
 margin-left: 20px;
}

.text_icon_container {
 width: 80%;
 height: 40px;
 display: flex;
 justify-content: space-between;
 align-items: center;
 margin-top: 0px;
 margin-right: 15px;
}

.text_icon_container > div {
 display: flex;
 align-items: center;
}

.text_icon_container > div > img {
 width: 35px;
 margin-left: 3px;
}

/* video */

.video_player {
 width: 100%;
 height: 100vh;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 position: absolute;
 background: rgba(165, 163, 163, 0.377);
 z-index: 10;
 margin-top: -35px;
}

.close_video {
 width: 25px;
 margin-left: 240px;
 margin-bottom: 5px;
}

/* INVITATION  */

.all_invitation {
 width: 100%;
 height: 100%; 
 position: relative;
 display: flex;
 justify-content: center;
 align-items: center;
}

.invitation_container {
 width: 80%;
 height: 60%; 

}

.date_time_container {
 width: 172px;
 height: 80px; 
 display: flex;
 justify-content: space-between;
 align-items: center;
 margin-left: 20px;
}

.date_start > p {
 font-size: 27px;
 font-weight:bolder;
 font-family: 'Quicksand', sans-serif;
 margin: 3px;
}

.line_divide {
 width: 3px;
 height: 100%;
 background-image: linear-gradient(#B38D5D, #FDC891);
}

.time_Start > p {
 font-size: 27px;
 font-weight: bold;
 font-family: 'Quicksand', sans-serif;
 margin: 3px;
}

.date_counter_container {
 width: 80%;
 height: 50px;
 display: flex;
 justify-content: space-between;

 margin: 50px auto;

}

.number_container {
 width: 55px;
 height: 57px;
 position: relative;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 background: black;
 color: #FFFF;
 font-size: 20px;
 font-weight:bolder;
 font-family: 'Quicksand', sans-serif;
 border-radius: 9px;
}

.number_container > span {
 width: 100%;
 height: 22px;
 position: relative;
 display: flex;
 align-items: center;
 justify-content: center;
 border-radius: 30%;
 background-image: linear-gradient(#B38D5D, #FDC891);
 font-size: 11px;
 top: 6px;
}

.we_got_married {
 width: 60%;
 height: 60px;
 text-align: center;
 font-family: 'Lobster Two', cursive;
 font-size: 24px;
 margin: 0 auto;
}

.btn_interact_container {
 width: 60%;
 height: 120px;
 margin: 0 auto;
}

.btn_card {
 width: 100%;
 height: 35px;
 display: flex;
 align-items: center;
 justify-content: center;
 background-image: linear-gradient(#B38D5D, #FDC891);
 color: #FFFF;
 text-align: center;
 font-family: 'Quicksand', sans-serif;
 border-radius: 9px;
 margin-bottom: 5px;
}

.btn_card > p {
 width: 71.2%;
}

.btn_card > a {
 width: 70%;
 text-decoration: none;
 color: #FFFF;
}

.btn_card > img {
 width: 20px;
 margin-left: 4px;
}

.dress_n_gift_container {
 width: 100%;
 height: 100vh;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 position: relative;
 background: rgba(165, 163, 163, 0.377);
 z-index: 10;
 margin-top: -35px;
}

.dress_code {
 width: 70%;
 height: 70%;
}

.dress_n_gift_container > div {
 width: 200px;
 position: absolute;
 top: 160px;
 color: #FFFF;
 font-family: 'Quicksand', sans-serif;
 border-radius: 9px;
 background-image: linear-gradient(#B38D5D, #FDC891);
 padding: 5px;

}

.card_bank_list {
 width: 70%;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 background-image: linear-gradient(#B38D5D, #FDC891);
 border-radius: 10%;
}

.count_info {
 width: 90%;
 text-align: center;
 color: black;
 font-family: 'Quicksand', sans-serif;
 border-bottom: 2px solid black;
 padding: 10px 0px;
}

.count_info > p {
 margin: 2px;
}


